import React, { useEffect, useState } from "react";
import {
  Box,
  Card,
  CardContent,
  Typography,
  Grid,
  Chip,
  Pagination,
  TextField,
  useTheme,
  useMediaQuery,
  Autocomplete,
  Avatar,
  Paper,
  Button,
} from "@mui/material";

import TrendingUpIcon from "@mui/icons-material/TrendingUp";
import TrendingDownIcon from "@mui/icons-material/TrendingDown";
import PendingIcon from "@mui/icons-material/Pending";
import {
  FaArrowDownLong,
  FaArrowUpLong,
  FaEyeSlash,
  FaEye,
} from "react-icons/fa6";
import Dashboardcard from "./Dashboardcard";
import {
  getDAshboardDataForASMZSM,
  getRetailersForASMZSM,
} from "../../../../api/login/Login";
import { Link } from "react-router-dom";
import UserGuard from "../../../../utils/UserGuard";

const customColors = {
  success: { iconBg: "#4CAF50", border: "#388E3C", text: "#2E7D32", label: "Success", key: "success" },
  failed: { iconBg: "#F44336", border: "#D32F2F", text: "#C62828", label: "Failed", key: "failed" },
  pending: { iconBg: "#FF9800", border: "#F57C00", text: "#EF6C00", label: "Pending", key: "pending" },
  refund: { iconBg: "#F44336", border: "#D32F2F", text: "#C62828", label: "Refund", key: "refund" },
  totalAmount: { iconBg: "#4CAF50", border: "#388E3C", text: "#2E7D32", label: "Total Amount", key: "totalAmount" },
  balance_reverse: { iconBg: "#FF9800", border: "#F57C00", text: "#EF6C00", label: "Balance Reverse", key: "balance_reverse" },
  balance_transfer: { iconBg: "#4CAF50", border: "#388E3C", text: "#2E7D32", label: "Balance Transfer", key: "balance_transfer" },
  cardGradient: "linear-gradient(135deg, #FFFFFF, #F7F9FC)",
  titleColor: "#1E88E5",
};

const obj = {
  success: { count: 0, totalAmount: 0 },
  pending: { count: 0, totalAmount: 0 },
  failed: { count: 0, totalAmount: 0 },
  refund: { count: 0, totalAmount: 0 },
};

const fundTransferObj = {
  totalAmount: 0,
  balance_reverse: 0,
  balance_transfer: 0,
  totalCount: 0,
};

const SummaryCard = ({ title, data, url }) => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down("sm"));

  return (
    <Card
      elevation={4}
      sx={{
        height: "100%",
        background: "var(--bs-primary1)",
        color: "var(--bs-primary2)",
        borderRadius: 3,
        p: 2,
        "&:hover": {
          boxShadow: "0 8px 20px rgba(0,0,0,0.15)",
          transform: "scale(1.02)",
          transition: "all 0.3s ease-in-out",
        },
      }}
    >
      <Link to={url}>
        <CardContent>
          <Typography
            variant={isMobile ? "h6" : "h5"}
            fontWeight="bold"
            gutterBottom
            sx={{
              color: "var(--bs-primary2)",
              textAlign: "center",
              mb: 3,
              fontSize: "16px",
            }}
          >
            {title}
          </Typography>
          <Grid container spacing={2}>
            {Object.keys(data)
              ?.filter((item) => !["totalCount"].includes(item))
              .map((status) => (
                <Grid item xs={12} key={status}>
                  {/* <Chip 
                    icon={status === 'success' ? <TrendingUpIcon /> : status === 'failed' ? <TrendingDownIcon /> : <PendingIcon />}
                    label={`${status.charAt(0).toUpperCase() + status.slice(1)}: ${data[status] ?? 'N/A'}`}
                    color={status === 'success' ? "success" : status === 'failed' ? "error" : "warning"}
                  /> */}

                  <Chip
                    avatar={
                      <Avatar
                        sx={{
                          bgcolor:
                            customColors[status]?.iconBg ||
                            customColors?.success?.iconBg,
                        }}
                      >
                        {status === "success" ? (
                          <TrendingUpIcon sx={{ fill: "#fff" }} />
                        ) : status === "failed" ? (
                          <TrendingDownIcon sx={{ fill: "#fff" }} />
                        ) : (
                          <PendingIcon sx={{ fill: "#fff" }} />
                        )}
                      </Avatar>
                    }
                    label={`${customColors?.[status]?.label || status}: ${
                      data?.[status]?.totalAmount ??
                      data?.[customColors?.[status]?.key] ??
                      "0.00"
                    }`}
                    variant="outlined"
                    sx={{
                      width: "100%",
                      justifyContent: "flex-start",
                      fontWeight: "bold",
                      p: 1,
                      borderColor: customColors[status]?.border,
                      "& .MuiChip-label": {
                        fontWeight: 600,
                        color: "var(--bs-primary2)",
                      },
                      "&:hover": {
                        backgroundColor: `${customColors[status]?.iconBg}20`,
                      },
                    }}
                  />
                </Grid>
              ))}
          </Grid>
        </CardContent>
      </Link>
    </Card>
  );
};

const PaginatedBusinessSummary = ({ data }) => {
  const today = new Date().toISOString().split("T")[0];
  const [page, setPage] = useState(1);
  const storedUserType = localStorage.getItem("userType");
  const [searchTerm, setSearchTerm] = useState("");
  const [retailers, setRetailers] = useState([]);
  const [selectedRetailer, setSelectedRetailer] = useState(null);
  const [dashboardData, setDashboardData] = useState();
  const itemsPerPage = 10;
  const theme = useTheme();
  const [fromDate, setFromDate] = useState(today);
  const [toDate, setToDate] = useState(today);
  const [submit, setSubmit] = useState(false);
  const isMobile = useMediaQuery(theme.breakpoints.down("sm"));

  const handleChangePage = (event, newPage) => setPage(newPage);

  const handleSearchChange = (event, newValue) => {
    setSearchTerm(newValue || "");
    setPage(1);
  };

  const summaryData = [
    {
      title: "Bank Transfer Summary",
      data: dashboardData?.dmtTxns ?? obj,
      url: `/super/bank-transfer-report?start-date=${fromDate}&end-date=${toDate}`,
    },
    {
      title: "AEPS Summary",
      data: dashboardData?.aepsTxns ?? obj,
      url: `/super/aeps-transfer-report?start-date=${fromDate}&end-date=${toDate}`,
    },
    {
      title: "BBPS Summary",
      data: dashboardData?.bbpsTxns ?? obj,
      url: `/super/bbps-report?start-date=${fromDate}&end-date=${toDate}`,
    },
    {
      title: "Fund Transfer Summary",
      data: dashboardData?.fundTransfer ?? fundTransferObj,
      url: `/super/fund-transfer-report?start-date=${fromDate}&end-date=${toDate}`,
    },
  ];

  const filteredData = summaryData.filter((item) =>
    item.title.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const pageCount = Math.ceil(filteredData.length / itemsPerPage);
  const displayedData = filteredData.slice(
    (page - 1) * itemsPerPage,
    page * itemsPerPage
  );

  const handleSubmit = () => setSubmit((pre) => !pre);

  const getDashboardData = async () => {
    let body = { start_date: fromDate, end_date: toDate };
    if (selectedRetailer) body.retailer_id = selectedRetailer?._id;

    let res = await getDAshboardDataForASMZSM(body);
    setDashboardData(res?.data?.data || null);
  };

  const getRetailers = async () => {
    let res = await getRetailersForASMZSM();
    if (res?.data?.error === false) setRetailers(res.data?.data || []);
    else setRetailers([]);
  };

  const handleReset = () => {
    setToDate("");
    setFromDate("");
    setSelectedRetailer(null);
    setSubmit((pre) => !pre);
  };

  useEffect(() => {
    getRetailers();
  }, []);

  useEffect(() => {
    getDashboardData();
  }, [submit]);

  return (
    <Box sx={{ flexGrow: 1 }}>
      {/*  Padding increased here */}
      <Card
        elevation={0}
        sx={{
          p: 4, // UPDATED
          pb: 2,
          mb: 3,
          borderRadius: 3,
          background: "var(--bs-primary3)",
          color: "var(--bs-primary2)",
          boxShadow: 0,
        }}
      >
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <Box
              sx={{
                p: 2,
                paddingX: 0,
                display: "flex",
                alignItems: "center",
                justifyContent: "start",
                gap: 2,
                width: "100%",
                flexWrap: "wrap",
              }}
            >
              <TextField
                label="From"
                type="date"
                size="small"
                value={fromDate}
                onChange={(e) => setFromDate(e.target.value)}
                InputLabelProps={{ shrink: true }}
                inputProps={{ max: today }}
                sx={{
                  flex: 1,
                  minWidth: 160,
                  background: "var(--bs-primary3)",
                  color: "var(--bs-primary2)",
                  "& .MuiInputBase-root": {
                    backgroundColor: "var(--bs-primary1)",
                  },
                  "& .MuiOutlinedInput-root": {
                    "&:hover fieldset": { borderColor: "var(--bs-primary3)" },
                    "&.Mui-focused fieldset": {
                      borderColor: "var(--bs-primary2)",
                    },
                  },
                }}
              />

              <TextField
                label="To"
                type="date"
                size="small"
                value={toDate}
                onChange={(e) => setToDate(e.target.value)}
                InputLabelProps={{ shrink: true }}
                inputProps={{
                  max: today,
                }}
                sx={{
                  flex: 1,
                  minWidth: 160,
                  background: "var(--bs-primary1)",
                  color: "var(--bs-primary2)",
                  "& .MuiInputBase-root": {
                    backgroundColor: "var(--bs-primary1)",
                  },
                  "& .MuiOutlinedInput-root": {
                    "&:hover fieldset": { borderColor: "var(--bs-primary3)" },
                    "&.Mui-focused fieldset": {
                      borderColor: "var(--bs-primary2)",
                    },
                  },
                }}
              />

              <UserGuard
                currentUser={storedUserType}
                excludeUserList={["Reseller"]}
              >
                <Autocomplete
                  options={retailers}
                  getOptionLabel={(option) => option.name}
                  value={selectedRetailer ?? null}
                  size="small"
                  onChange={(e, newVal) => setSelectedRetailer(newVal)}
                  sx={{ flex: 1, minWidth: 220, flexShrink: 0 }}
                  renderOption={(props, option) => (
                    <li {...props} key={option.retailer_id}>
                      <Box
                        sx={{
                          fontSize: "13px",
                          color: "var(--bs-primary2)",
                        }}
                        display="flex"
                        columnGap={1.5}
                        alignItems="center"
                      >
                        {option.name}
                      </Box>
                    </li>
                  )}
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      label="Select Retailer"
                      InputLabelProps={{
                        sx: {
                          "&.MuiInputLabel-root:not(.MuiInputLabel-shrink)": {
                            top: "-3px",
                          },
                          color: "var(--bs-primary2)",
                          "& .MuiInputBase-root": {
                            backgroundColor: "var(--bs-primary1)",
                          },
                          "& .MuiOutlinedInput-root": {
                            "&:hover fieldset": {
                              borderColor: "var(--bs-primary3)",
                            },
                            "&.Mui-focused fieldset": {
                              borderColor: "var(--bs-primary2)",
                            },
                          },
                        },
                      }}
                    />
                  )}
                />
              </UserGuard>
            </Box>
          </Grid>

          <Grid item xs={12}>
            <button
              style={{
                background: "var(--bs-primary1)",
                color: "var(--bs-primary2)",
                padding: "6px 24px",
                borderRadius: "8px",
                marginRight: 12,
              }}
              type="button"
              className="btn"
              onClick={handleSubmit}
            >
              Search
            </button>

            <button
              style={{
                background: "var(--bs-primary1)",
                color: "var(--bs-primary2)",
                padding: "6px 24px",
                borderRadius: "8px",
              }}
              type="button"
              className="btn"
              onClick={handleReset}
            >
              Reset
            </button>
          </Grid>
        </Grid>
      </Card>

      <Dashboardcard data={dashboardData} />

      <UserGuard currentUser={storedUserType} excludeUserList={["Reseller"]}>
        {/*  Padding improved here */}
        <Card
          sx={{
            p: 4,
            pb: 2,
            mt: 3,
            borderRadius: 3,
            boxShadow: 0,
            background: "var(--bs-primary1)",
            color: "var(--bs-primary2)",
          }}
        >
          <Typography
            variant="h4"
            fontSize={"20px"}
            mb={2}
            gutterBottom
            fontWeight="bold"
            color="var(--bs-primary2)"
            align="center"
          >
            Business Summary
          </Typography>

          {/* Search Bar with Autocomplete */}
          {/* <Autocomplete
            freeSolo
        options={summaryData.map(item => item.title)} // List of service titles for suggestions
        onInputChange={handleSearchChange} // Handle search input changes
        renderInput={(params) => (
          <TextField
            {...params}
            variant="outlined"
            placeholder="Search by title..."
            fullWidth
            sx={{ mb: 4 ,  "& .MuiOutlinedInput-root": {
              "&:hover fieldset": { borderColor: "var(--bs-primary1)" }, // Hover border
              "&.Mui-focused fieldset": { borderColor: "var(--bs-primary1)" }, // Focus border
            }, }}
          />
        )}
          /> */}

          <Grid
            container
            spacing={3}
            sx={{
              mb: 4,
              color: "var(--bs-primary2)",
              backgroundColor: "var(--bs-primary1)",
            }}
          >
            {displayedData.length > 0 ? (
              displayedData.map((item, index) => (
                <Grid item xs={12} md={6} key={index}>
                  <SummaryCard title={item.title} data={item.data} url={item.url} />
                </Grid>
              ))
            ) : (
              <Grid item xs={12}>
                <Typography
                  variant="h6"
                  color="var(--bs-primary2)"
                  align="center"
                >
                  No data found for "{searchTerm}"
                </Typography>
              </Grid>
            )}
          </Grid>

          {/* Pagination */}
          {/* <Box display="flex" justifyContent="center">
        <Pagination 
          count={pageCount} 
          page={page} 
          onChange={handleChangePage} 
          color="primary" 
          size="large"
          disabled={pageCount <= 1}
        />
          </Box> */}
        </Card>
      </UserGuard>
    </Box>
  );
};

export default PaginatedBusinessSummary;
