import {
  Avatar,
  Grid,
  Stack,
  Typography,
  Box,
  Button,
  Divider,
} from "@mui/material";
import DasBoardCheck from "../../../components/admin/daseboardCheck/DasBoardChaeck";
import { genrateUrl } from "../../../utils/genrateUrl";
import ProfileModal from "./ProfileModal";
import { useState } from "react";
import useWalletReport from "../../../hooks/useWalletReport";
import { useNavigate } from "react-router-dom";
import Transactions from "./Transactions";

function DaseboardCheckPage({
  profiledata,
  walletData,
  services,
  position,
  isInstantpaypresnt,
}) {
  const navigate = useNavigate();
  const [showModal, setShowModal] = useState(false);
  const [showBalance, setShowBalance] = useState(false);

  const { data } = useWalletReport();

  return (
    <Grid
      container
      sx={{
        height: "100%",
        overflow: "auto",
        backgroundColor: "var(--bs-primary3)",
      }}
    >
      {/* Left scrollable section */}
      <Grid
        item
        xs={12}
        md={10.2}
        sx={{
          height: "100%",
          overflowY: "auto",
          pr: 2,
          scrollbarWidth: "none",
          "&::-webkit-scrollbar": { display: "none" },
        }}
      >
        <DasBoardCheck
          walletData={walletData}
          services={services}
          position={position}
          isInstantpaypresnt={isInstantpaypresnt}
        />
      </Grid>

      {/* Right fixed sidebar */}
      <Grid
        item
        xs={12}
        md={1.8}
        sx={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          gap: 2,
        }}
      >
        {/* PROFILE SECTION */}
        <Box
          sx={{
            width: "100%",
            color: "var(--bs-primary2)",
            borderRadius: "16px",
            boxShadow: "0 4px 10px rgba(0,0,0,0.05)",
            p: 2,
            textAlign: "center",
            backgroundColor: "var(--bs-primary1)",
          }}
        >
          <Stack alignItems="center" spacing={1}>
            <Avatar
              src={genrateUrl(profiledata?.profile)}
              sx={{
                width: 70,
                height: 70,
                border: "2px solid var(--bs-primary3)",
                boxShadow: "0 0 10px rgba(0,0,0,0.1)",
              }}
            />
            <Typography
              variant="subtitle1"
              sx={{
                color: "var(--bs-primary2)",
                whiteSpace: "nowrap",
                overflow: "hidden",
                textOverflow: "ellipsis",
                fontWeight: 600,
                width: "100%",
              }}
            >
              {profiledata?.name || ""}
            </Typography>

            {/* Wallet Balance */}
            <Typography
              variant="body2"
              sx={{ color: "var(--bs-primary2)", fontSize: "14px" }}
            >
              Balance: {showBalance ? profiledata?.main_wallet || 0 : "XXXX"}
            </Typography>
            {/* Toggle Balance Button */}
            <Box
              onClick={() => setShowBalance(!showBalance)}
              sx={{
                mt: 1,
                width: "130px",
                height: "38px",
                borderRadius: "50px",
                backgroundColor: showBalance ? "#28a745" : "#dc3545",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                cursor: "pointer",
                transition: "all 0.35s ease",
                position: "relative",
                overflow: "hidden",
                userSelect: "none",
                color: "white",
                fontSize: "14px",
                fontWeight: 700,
              }}
            >
              {/* Sliding Circle */}
              <Box
                sx={{
                  position: "absolute",
                  left: showBalance ? "calc(100% - 34px)" : "6px",
                  width: "26px",
                  height: "26px",
                  borderRadius: "50%",
                  backgroundColor: "white",
                  transition: "all 0.35s ease",
                  boxShadow: "0 2px 6px rgba(0,0,0,0.3)",
                }}
              />

              {/* Dynamic Text */}
              <span style={{ position: "relative", zIndex: 2 }}>
                {showBalance ? "Hide" : "Show"}
              </span>
            </Box>
            {/* Profile Modal View */}
            <Button
              variant="outlined"
              size="small"
              sx={{
                textTransform: "none",
                fontSize: "13px",
                mt: 1,
                width: "100%",
                color: "var(--bs-primary2)",
                borderColor: "var(--bs-primary3)",
              }}
              onClick={() => setShowModal(true)}
            >
              View Profile
            </Button>
          </Stack>
        </Box>

        {/* HELP CENTER SECTION */}
        <Box
          sx={{
            width: "100%",
            color: "var(--bs-primary2)",
            borderRadius: "16px",
            boxShadow: "0 4px 10px rgba(0,0,0,0.05)",
            p: 2,
            backgroundColor: "var(--bs-primary1)",
          }}
        >
          <Typography
            variant="h6"
            sx={{
              fontSize: "16px",
              fontWeight: 600,
              color: "var(--bs-primary2)",
              mb: 1,
              textAlign: "center",
            }}
          >
            Help Center
          </Typography>

          <Typography
            variant="body2"
            sx={{
              fontSize: "13px",
              textAlign: "center",
              color: "var(--bs-primary2)",
            }}
          >
             +91 1242262222
          </Typography>
          <Typography
            variant="body2"
            sx={{
              fontSize: "13px",
              textAlign: "center",
              color: "var(--bs-primary2)",
            }}
          >
             support@bankmudraa.com
          </Typography>

          <Button
            variant="outlined"
            size="small"
            sx={{
              textTransform: "none",
              fontSize: "13px",
              mt: 1.5,
              borderColor: "var(--bs-primary3)",
              color: "var(--bs-primary2)",
              width: "100%",
            }}
            onClick={() =>
            (window.location.href =
              "mailto:support@bankmudraa.com?subject=Support Request&body=Hello BankMudraa Support,")
            }
          >
            Contact Us
          </Button>
        </Box>

        {/* LATEST TRANSACTIONS SECTION */}
        <Box
          sx={{
            width: "100%",
            color: "var(--bs-primary2)",
            borderRadius: "16px",
            boxShadow: "0 4px 10px rgba(0,0,0,0.05)",
            p: 2,
            backgroundColor: "var(--bs-primary1)",
          }}
        >
          <Typography
            variant="h6"
            sx={{
              fontSize: "16px",
              fontWeight: 600,
              color: "var(--bs-primary2)",
              mb: 1,
              textAlign: "center",
            }}
          >
            Recent Transactions
          </Typography>

          {/* {transactions.map((txn, index) => (
            <Box key={txn.id}>
              <Box
                sx={{
                  display: "flex",
                  justifyContent: "space-between",
                  py: 0.5,
                  color: "var(--bs-primary2)",
                }}
              >
                <Typography variant="body2" sx={{ fontSize: "13px" }}>
                  {txn.title}
                </Typography>
                <Typography
                  variant="body2"
                  sx={{ fontWeight: 600, fontSize: "13px" }}
                >
                  {txn.amount}
                </Typography>
              </Box>
              {index < transactions.length - 1 && <Divider />}
            </Box>
          ))} */}

          <Transactions data={data} />

          <Button
            variant="text"
            size="small"
            onClick={() => navigate("/wallet-report")}
            sx={{
              textTransform: "none",
              fontSize: "13px",
              mt: 1,
              color: "var(--bs-primary2)",
              width: "100%",
            }}
          >
            View More 
          </Button>
        </Box>
      </Grid>

      {/* BOOTSTRAP PROFILE MODAL */}
      <ProfileModal
        show={showModal}
        handleClose={() => setShowModal(false)}
        profiledata={profiledata}
      />
    </Grid>
  );
}

export default DaseboardCheckPage;
