import { useState } from "react";
import { Box, Typography, Divider, Dialog, Button } from "@mui/material";

export default function Transactions({ data }) {
  const [selectedTxn, setSelectedTxn] = useState(null);

  return (
    <Box sx={{ width: "100%", bgcolor: "#fff", p: 1, borderRadius: 2 }}>
      {data.slice(0, 5).map((txn, index) => (
        <Box key={txn._id}>
          <Box
            onClick={() => setSelectedTxn(txn)}
            sx={{
              display: "flex",
              justifyContent: "space-between",
              alignItems: "center",
              px: 1,
              py: 1.4,
              gap: 1,
              flexWrap: "nowrap",
              cursor: "pointer",
            }}
          >
            {/* LEFT SIDE */}
            <Box sx={{ flex: 1, minWidth: 0, overflow: "hidden" }}>
              <Typography
                sx={{
                  fontSize: "14px",
                  fontWeight: 600,
                  color: "#222",
                  whiteSpace: "nowrap",
                  overflow: "hidden",
                  textOverflow: "ellipsis",
                }}
              >
                {txn.message}
              </Typography>

              <Typography
                sx={{
                  fontSize: "12px",
                  color: "#777",
                  whiteSpace: "nowrap",
                }}
              >
                {txn.createdAt}
              </Typography>
            </Box>

            {/* Amount */}
            <Typography
              sx={{
                fontWeight: 700,
                fontSize: "15px",
                whiteSpace: "nowrap",
                color: txn.type === "credit" ? "green" : "red",
              }}
            >
              {txn.type === "credit" ? `+ ${txn.amount}` : `- ${txn.amount}`}
            </Typography>
          </Box>
          {index < 4 && <Divider sx={{ opacity: 0.4, my: 0.5 }} />}
        </Box>
      ))}

      <Dialog
        open={Boolean(selectedTxn)}
        onClose={() => setSelectedTxn(null)}
        sx={{
          "& .MuiPaper-root": {
            width: "380px",
            borderRadius: 4,
            p: 3,
          },
        }}
      >
        {selectedTxn && (
          <Box>
            {/* HEADER */}
            <Typography
              sx={{
                fontSize: "18px",
                fontWeight: 700,
                textAlign: "center",
                mb: 1,
              }}
            >
              Transaction Details
            </Typography>

            <Divider sx={{ my: 2 }} />

            {/* MAIN MESSAGE */}
            <Typography sx={{ fontSize: "16px", fontWeight: 600, mb: 2 }}>
              {selectedTxn.message}
            </Typography>

            {/* AMOUNT BOX */}
            <Box
              sx={{
                bgcolor: selectedTxn.type === "credit" ? "#e8fbe8" : "#fde8e8",
                p: 2,
                borderRadius: 3,
                textAlign: "center",
                mb: 2,
              }}
            >
              <Typography
                sx={{
                  fontSize: "22px",
                  fontWeight: 700,
                  color: selectedTxn.type === "credit" ? "green" : "red",
                }}
              >
                {selectedTxn.type === "credit"
                  ? `+ ${selectedTxn.amount}`
                  : `- ${selectedTxn.amount}`}
              </Typography>
            </Box>

            {/* DETAILS */}
            <Box sx={{ display: "grid", gap: 1.5 }}>
              <Box>
                <Typography sx={{ fontSize: "12px", color: "#777" }}>
                  Transaction Type
                </Typography>
                <Typography sx={{ fontWeight: 600 }}>
                  {selectedTxn.trans_type}
                </Typography>
              </Box>

              <Box>
                <Typography sx={{ fontSize: "12px", color: "#777" }}>
                  Date
                </Typography>
                <Typography sx={{ fontWeight: 600 }}>
                  {selectedTxn.createdAt}
                </Typography>
              </Box>

              <Box>
                <Typography sx={{ fontSize: "12px", color: "#777" }}>
                  Old Balance
                </Typography>
                <Typography sx={{ fontWeight: 600 }}>
                  {selectedTxn.o_bal}
                </Typography>
              </Box>

              <Box>
                <Typography sx={{ fontSize: "12px", color: "#777" }}>
                  New Balance
                </Typography>
                <Typography sx={{ fontWeight: 600 }}>
                  {selectedTxn.c_bal}
                </Typography>
              </Box>

              <Box>
                <Typography sx={{ fontSize: "12px", color: "#777" }}>
                  Txn ID
                </Typography>
                <Typography sx={{ fontWeight: 600, wordBreak: "break-all" }}>
                  {selectedTxn.txn_id}
                </Typography>
              </Box>

              <Box>
                <Typography sx={{ fontSize: "12px", color: "#777" }}>
                  Order ID
                </Typography>
                <Typography sx={{ fontWeight: 600, wordBreak: "break-all" }}>
                  {selectedTxn.order_id}
                </Typography>
              </Box>
            </Box>

            <Divider sx={{ my: 3 }} />
          </Box>
        )}
      </Dialog>
    </Box>
  );
}
